.header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    padding-bottom: 5px;
   
}

 .Logo_header {
        width: 100px;
    }


.active{
    background: linear-gradient(315deg, #f9d29d 0%, #ffd8cb 74%);
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    color: #8A6E2F;
    position: sticky;
    top: 0;
}

nav .title {
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: #8A6E2F;
}

nav ul {
    display: flex;
}

nav ul li {
   list-style: none; 
}

nav ul li a {
    display: block;
    text-decoration: none;
    color: #8A6E2F;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
}

nav ul li a:not(.active):hover {
    background: linear-gradient(315deg, #f9d29d 0%, #ffd8cb 74%);
}

nav .menu {
    display: none;
    position: absolute;
    top: 1.8rem;
    right: 1rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}

nav .menu span {
    height: 0.4rem;
    width: 100%;
    background-color: #8A6E2F;
    border-radius: 0.2rem;
}


@media (max-width: 480px) {
    .header {
        justify-content: center;
    }
    nav .menu {
        display: flex;
    }

    nav {
        flex-direction: column;
        align-items: flex-start;
    }

    nav ul {
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }

    nav ul.open {
        display: flex;
        padding: 0;
    }

    nav ul li {
        width: 100%;
        text-align: center;
    }

    nav ul li a {
        margin: 0.2rem 0.5rem;
    }
}

